<template>
    <div>
        <v-card flat class="pa-5 d-flex justify-center align-center">
            <v-card width="900px" class="pa-2">
                <v-row>
                    <v-col class="text-center pt-5 mb-0 pb-0" cols="12">
                        <v-avatar size="120">
                            <v-img
                                :alt="`${patientDetails.f_name} ${patientDetails.l_name} avatar`"
                                :src="patientDetails.gender == 1 ? '/storage/upload/pp_1.png' : '/storage/upload/pp_2.png'">
                            </v-img>
                        </v-avatar>
                    </v-col>
                    <v-col class="text-center mt-0 pt-0 mb-0 pb-0" cols="12">
                        <h2>{{patientDetails.f_name+", "+patientDetails.m_name+" "+patientDetails.l_name}}</h2>
                    </v-col>
                    <v-col class="text-center mt-0 pt-0" cols="12">
                       <div class="d-flex justify-content-center">
                           <div class="px-1"><strong><v-icon color="danger">mdi-home</v-icon>Purok:</strong> {{patientDetails.purok.name}}</div>
                            <div class="px-2"><strong>Age:</strong> {{patientDetails.age}}</div>
                            <div class="px-1"><strong><v-icon color="info">mdi-gender-female</v-icon>Gender:</strong> {{patientDetails.gender == 1 ? 'Male' : 'Female'}}</div>
                       </div>
                    </v-col>
                    <v-col cols="12" class="px-3">
                        <v-data-table :search="search" :headers="headers" :items="recordList" :items-per-page="10" class="elevation-0">                                
                              <template v-slot:body="{ items }">
                                  <tbody>
                                      <tr v-for="(item, index) in items" :key="index">
                                          <td width="20%">{{item.l_name+" "+item.m_name+" "+item.f_name}}</td>
                                          <td>
                                            <v-chip v-if="item.gender == 1" small dark class="ma-2" color="blue" >
                                              Male
                                            </v-chip>
                                             <v-chip v-else small dark class="ma-2" color="red" >
                                              Female
                                            </v-chip>
                                          </td>
                                          <td>{{item.status}}</td>
                                          <td width="10%">{{item.b_date}}</td>
                                          <td>{{item.age}}</td>
                                          <td>{{item.purok ? item.purok.name : ''}}</td>
                                           <td>{{item.cell_number}}</td>
                                          <td width="30%">
                                             <v-btn small dark @click="viewPatient(item.id)" rounded color="primary">
                                                  <v-icon small>
                                                      mdi-eye
                                                  </v-icon>
                                                  View
                                              </v-btn>
                                              <v-btn small @click="openUpdateDialog(item.id)" rounded color="info">
                                                  <v-icon small>
                                                      mdi-pencil
                                                  </v-icon>
                                                  Edit
                                              </v-btn>
                                              <v-btn small @click="showDeletePrompt(item.id)" dark rounded color="danger" >
                                                  <v-icon small>
                                                      mdi-delete
                                                  </v-icon>
                                                  Delete
                                              </v-btn>
                                          </td>
                                      </tr>
                                      <tr v-if="recordList.length == 0">
                                          <td colspan="42" class="text-center"> No data available</td>
                                      </tr>
                                  </tbody>
                              </template>
                          </v-data-table>
                    </v-col>
                </v-row>
            </v-card>
        </v-card>
    </div>
</template>
<script>
export default {
    data() {
        return {
            patientDetails: [],
            recordList: [],
            headers: [
                {text: 'Date', align: 'start',},
                { text: 'Blood Pressure', value: 'gender'},
                { text: 'Temperature', value: 'status'},
                { text: 'Medicine Given', value: 'b_date' },
                { text: 'Remarks', value: 'age' },
                { text: 'Action', },
            ],
        }
    },
    methods:{
        async getPatientDetails(){
            axios.get(`/api/patient_information/view/${this.$route.params.id}`)
            .then((res)=>{
                if(res.data.success){
                    this.patientDetails = res.data.data
                }
            })
        }
    },
    beforeMount(){
        this.getPatientDetails();
    }
}
</script>