<template>
    <div>
        <v-card flat class="pa-5 d-flex justify-center align-center">
            <v-card width="900px" class="pa-2">
                <v-row>
                    <v-col class="text-center pt-5 mb-0 pb-0" cols="12">
                        <v-avatar size="120">
                            <v-img
                                :alt="`${patientDetails.f_name} ${patientDetails.l_name} avatar`"
                                :src="patientDetails.gender == 1 ? '/storage/upload/pp_1.png' : '/storage/upload/pp_2.png'">
                            </v-img>
                        </v-avatar>
                       
                    </v-col>
                    <v-col class="text-center mt-0 pt-0 mb-0 pb-0" cols="12">
                        <h2>{{patientDetails.f_name+", "+patientDetails.m_name+" "+patientDetails.l_name}}</h2>
                    </v-col>
                    <v-col class="text-center mt-0 pt-0" cols="12">
                       <div class="d-flex justify-content-center">
                            <div class="px-1"><strong><v-icon color="danger">mdi-home</v-icon>Purok:</strong> {{patientDetails.purok.name}}</div>
                            <div class="px-2"><strong>Age:</strong> {{patientDetails.age}}</div>
                            <div class="px-1"><strong><v-icon color="info">mdi-gender-female</v-icon>Gender:</strong> {{patientDetails.gender == 1 ? 'Male' : 'Female'}}</div>
                       </div>
                    </v-col>
                    <v-col cols="12" class="px-10 mt-3">
                        <v-row>
                            <v-col cols="12" class="my-0 py-0" md="6">
                                <div class="pb-2 font-weight-bold">Temperature</div>
                                <div class="d-flex">
                                    <v-text-field  placeholder="temperature" dense small type="number" color="primary" outlined />
                                </div>
                            </v-col>
                            <v-col cols="12" class="my-0 py-0" md="6">
                                <div class="pb-2 font-weight-bold">Blood Pressure</div>
                                <div class="d-flex">
                                    <v-text-field  placeholder="Up" dense small type="number" color="primary" outlined />
                                    <div style="font-size:20px" class="px-2 mt-1 font-weight-bold">/</div>
                                    <v-text-field  placeholder="Down" dense small type="number" color="primary" outlined />
                                </div>
                            </v-col>
                            <v-col cols="12" class="my-0 py-0" md="6">
                                <div class="pb-2 font-weight-bold">Pregnant</div>
                                <v-select
                                    dense
                                    item-text="text"
                                    item-value="val"
                                    :items="pregnant"
                                    outlined
                                    color="primary"
                                    placeholder="Pregnant"
                                ></v-select>
                            </v-col>
                            <v-col cols="12" class="my-0 py-0" md="6">
                                <div class="pb-2 font-weight-bold">Months of Pregnancy</div>
                                 <v-select
                                    dense
                                    :items="month_of_pregnant"
                                    outlined
                                    color="primary"
                                    placeholder="Number of months pregnant"
                                ></v-select>
                            </v-col>
                            <v-col cols="12" class="my-0 py-0" md="6">
                                <div class="pb-2 font-weight-bold">Related Illness</div>
                                <v-text-field  placeholder="Illness" dense small type="number" color="primary" outlined />
                            </v-col>
                            <v-col cols="12" class="my-0 py-0" md="6">
                                <div class="pb-2 font-weight-bold">Given Medicine</div>
                                <v-text-field  placeholder="GIven Medicine" dense small type="number" color="primary" outlined />
                            </v-col>
                            <v-col cols="12" class="my-0 py-0" md="12">
                                <div class="pb-2 font-weight-bold">Check-Up Details</div>
                                <v-textarea placeholder="Check-up details" dense small rows="3" color="primary" outlined />
                            </v-col>
                            <v-col cols="12" class="pb-7 pt-0 mt-0" md="12">
                                <v-btn class="px-10"  rounded color="primary" >
                                    <v-icon left>mdi-lock</v-icon>
                                        Submit
                                    </v-btn>
                            </v-col>
                        </v-row>
                    </v-col>
                </v-row>
            </v-card>
        </v-card>
    </div>
</template>
<script>
export default {
    data() {
        return {
            patientDetails: [],
            recordList: [],
            headers: [
                {text: 'Date', align: 'start',},
                { text: 'Blood Pressure', value: 'gender'},
                { text: 'Temperature', value: 'status'},
                { text: 'Medicine Given', value: 'b_date' },
                { text: 'Remarks', value: 'age' },
                { text: 'Action', },
            ],
            pregnant:[{text: 'YES', val: true},{text: 'NO', val: false}],
            month_of_pregnant: [],
        }
    },
    methods:{
        async getPatientDetails(){
            axios.get(`/api/patient_information/view/${this.$route.params.id}`)
            .then((res)=>{
                if(res.data.success){
                    this.patientDetails = res.data.data
                }
            })
        }
    },
    beforeMount(){
        this.getPatientDetails();
        for (let i = 0; i < 10; i++) {
            this.month_of_pregnant.push(i+1)
        }
    }
}
</script>